import { useCallback, useMemo, useRef, useState } from "react";
import { useDispatch } from "react-redux";
import axios from "axios";
import { setRequests } from "../../redux/requests/requests-actions";

export interface IContributor {
  id: number;
  Box: number;
  Name: string;
  Family: string;
  "Civil Number": string;
  "Date of Birth": string;
  Type: string;
  Region: string;
  Parcel: string;
  House: string;
  Street: string;
  "Date of Joining": string;
  "Automated Number": string;
  Search: string;
}

export enum TableState {
  loading,
  loaded,
  error,
}
interface IResponse {
  count?: number;
  list: IContributor[];
  key: number;
}

export const useApi = () => {
  const dispatch = useDispatch();
  const [count, setCount] = useState<any>(0);
  const keyRef = useRef<number>(0);
  const [state, setState] = useState<TableState>(TableState.loading);
  const fetch = useCallback(
    async (query: string, pageNum: number, gender: any) => {
      keyRef.current = Math.random();
      try {
        setState(TableState.loading);
        const { data: resData }: { data: IResponse } = await axios.post(
          "/listUsers",
          {
            pageNum,
            query,
            key: keyRef.current,
            gender,
          }
        );
        if (keyRef.current === resData.key) {
          setState(TableState.loaded);
          dispatch(setRequests(resData.list));
          if (typeof resData.count === "number") setCount(resData.count);
        }
      } catch (error) {
        setState(TableState.error);
      }
    },
    [setState, setCount, dispatch]
  );
  const pages = useMemo(() => {
    if (count > 50) return Math.ceil(count / 50);
    return 1;
  }, [count]);
  return { fetch, state, count, pages };
};
