import React, { useEffect } from "react";
import {
  Container,
  Row,
  Col,
  Table,
  Pagination,
  Spinner,
  Badge,
} from "react-bootstrap";
import "./custom-users-table.scss";
import { ReactComponent as Trash } from "../../assets/icons/delete.svg";
import { ReactComponent as Edit } from "../../assets/icons/edit.svg";
import { TableState, useApi } from "./custom-users-table-utils";
import { connect, useSelector } from "react-redux";
import { setSelectedPage } from "../../redux/table/table-actions";
import { useCookies } from "react-cookie";
import { setRequests } from "./../../redux/requests/requests-actions";
import BootstrapSwitchButton from "bootstrap-switch-button-react";
import axios from "axios";
import DeleteUserModal from "./../delete-user-modal/delete-user-modal";
import { setEditContributorData } from "./../../redux/table/table-actions";
import EditUserModal from "../edit-user-modal/edit-user-modal";
import { Toast } from "./../toaster/toaser";
interface CustomTableProps {
  query: string;
  selectedPage: number;
  setSelectedPage: (selectedPage: number) => void;
  setRequests: any;
  data: any[];
  setSelectedUserData: (data: any) => void;
}

const CustomUsersTable = ({
  query,
  selectedPage,
  setSelectedPage,
  setRequests,
  data,
  setSelectedUserData,
}: CustomTableProps) => {
  const [cookies] = useCookies(["userdata"]);
  const { fetch, state, count, pages } = useApi();
  const genderFilter = useSelector<any>((state) => state.table.genderFilter);
  useEffect(() => {
    fetch(query, selectedPage - 1, genderFilter);
  }, [query, selectedPage, fetch, genderFilter]);
  const columns = [
    "",
    " ",
    " ",
    "",
    " ",
    " ",
    "",
  ];
  const [showDelete, setShowDelete] = React.useState<boolean>(false);
  const [showEdit, setShowEdit] = React.useState<boolean>(false);
  return (
    <>
      <Container fluid className="table-container">
        <Row className="table-body-container">
          <Col>
            <div className="table-body-scroller">
              {state === TableState.loading ? (
                <Spinner animation="border" />
              ) : (
                <Table striped bordered hover>
                  <thead id="old-hidden-header">
                    <tr>
                      {columns.map((colTitle, i) => (
                        <th key={i}>{colTitle}</th>
                      ))}
                    </tr>
                  </thead>
                  <tbody>
                    {data.map((user) => (
                      <tr key={user.id}>
                        <td>{user.name}</td>
                        <td>{user.email}</td>
                        <td>{user.mobnum}</td>
                        <td>{user.gender ? "" : ""}</td>
                        <td>{user["date of birth"]?.split("T")[0]}</td>
                        <td>
                          {/* {user.active && user.status ? (
                            <Badge variant="success"></Badge>
                          ) : (
                            <Badge variant="danger"> </Badge>
                          )} */}
                          <BootstrapSwitchButton
                            width={90}
                            onlabel=""
                            offlabel=" "
                            checked={user.status}
                            onstyle="outline-success"
                            offstyle="outline-danger"
                            onChange={(checked: boolean) => {
                              axios
                                .post("/handleEnDisUserAcc", {
                                  id: user.id,
                                  status: checked,
                                })
                                .then(() =>
                                  setRequests(
                                    data.map((usr) =>
                                      usr.id !== user.id
                                        ? usr
                                        : {
                                            ...usr,
                                            status: checked,
                                            active: checked
                                              ? checked
                                              : usr.active,
                                          }
                                    )
                                  )
                                )
                                .catch(() =>
                                  Toast(
                                    "       ",
                                    false
                                  )
                                );
                            }}
                            size="sm"
                            disabled={user.id === 1}
                          />
                        </td>
                        <td>
                          <div
                            style={{
                              display: "flex",
                              justifyContent: "space-evenly",
                            }}
                          >
                            <div
                              className="table-action"
                              onClick={() => {
                                setSelectedUserData(user);
                                setShowEdit(true);
                              }}
                            >
                              <Edit fill="#007BFF" width="20px" />
                            </div>
                            <div
                              className="table-action"
                              onClick={() => {
                                if (user.id !== 1) {
                                  setSelectedUserData({
                                    id: user.id,
                                    name: user.name,
                                  });
                                  setShowDelete(true);
                                } else Toast("    ", false);
                              }}
                            >
                              <Trash
                                fill={user.id === 1 ? "#3d3d3d" : "#DC3645"}
                                width="20px"
                              />
                            </div>
                          </div>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </Table>
              )}
            </div>
          </Col>
        </Row>
        <Row className={"table-pagination"}>
          <Col>
            <Pagination color="#343A40" className="pagination">
              <span className="table-pagination-info">
                  {selectedPage * 50 - 49} {" "}
                {pages === selectedPage || count < 50
                  ? count
                  : selectedPage * 50}{" "}
                  {count} 
              </span>
              <Pagination.First
                disabled={selectedPage === 1}
                onClick={() => setSelectedPage(1)}
              />
              <Pagination.Prev
                disabled={selectedPage === 1}
                onClick={() => setSelectedPage(selectedPage - 1)}
              />
              {Array(2)
                .fill(0)
                .map((r, i) =>
                  selectedPage > 2 - i ? (
                    <Pagination.Item
                      key={i}
                      onClick={() => setSelectedPage(selectedPage - (2 - i))}
                      className="paginationItemStyle"
                    >
                      {selectedPage - (2 - i)}
                    </Pagination.Item>
                  ) : null
                )}
              <Pagination.Item active>{selectedPage}</Pagination.Item>
              {Array(2)
                .fill(0)
                .map((r, i) =>
                  pages >= selectedPage + (i + 1) ? (
                    <Pagination.Item
                      key={i}
                      onClick={() => setSelectedPage(selectedPage + (i + 1))}
                    >
                      {selectedPage + (i + 1)}
                    </Pagination.Item>
                  ) : null
                )}
              <Pagination.Next
                disabled={selectedPage >= pages}
                onClick={() => setSelectedPage(selectedPage + 1)}
              />
              <Pagination.Last
                disabled={selectedPage >= pages}
                onClick={() => setSelectedPage(pages)}
              />
            </Pagination>
          </Col>
        </Row>
      </Container>

      <DeleteUserModal
        resetUserData={() => setSelectedUserData({})}
        show={showDelete}
        setShow={setShowDelete}
      />
      <EditUserModal
        resetUserData={() => setSelectedUserData({})}
        show={showEdit}
        setShow={setShowEdit}
      />
    </>
  );
};

const mapStateToProps = (state: any) => ({
  query: state.table.query,
  selectedPage: state.table.selectedPage,
  data: state.requests.requests,
});

const mapDispatchToProps = (dispatch: any) => ({
  setRequests: (data: any) => dispatch(setRequests(data)),
  setSelectedPage: (page: number) => dispatch(setSelectedPage(page)),
  setSelectedUserData: (data: any) => dispatch(setEditContributorData(data)),
});

export default connect(mapStateToProps, mapDispatchToProps)(CustomUsersTable);
