import React from "react";
import {
  Modal,
  Button,
  InputGroup,
  FormControl,
  Col,
  Form,
  Row,
} from "react-bootstrap";
import "./edit-user-modal.scss";
import { setEditContributorData } from "../../redux/table/table-actions";
import editUserInputsData from "./edit-user-modal-data";
import axios from "axios";
import { Toast } from "../toaster/toaser";
import { useDispatch, useSelector } from "react-redux";
import { setRequests } from "./../../redux/requests/requests-actions";

interface EditUserModalProps {
  show: boolean;
  setShow: (show: boolean) => void;
  resetUserData: () => void;
}

const EditUserModal = ({
  show,
  setShow,
  resetUserData,
}: EditUserModalProps) => {
  const editUserData = useSelector(
    (state: any) => state.table.editContributorData
  );
  const users = useSelector((state: any) => state.requests.requests);
  const dispatch = useDispatch();
  const editAndAddInputs = editUserInputsData(editUserData);

  // useEffect(() => {
  //   const date = document.querySelectorAll("input")[3];
  //   if (String(users["Civil Number"]).length !== 12) {
  //     date?.setCustomValidity("      ");
  //   } else {
  //     date?.setCustomValidity("");
  //   }
  // }, [users]);

  const handleClose = () => {
    resetUserData();
    setShow(false);
  };

  const handleClickSubmit = (e: any) => {
    e.preventDefault();
    axios
      .post("/editUser", editUserData)
      .then(() => {
        Toast("  " + editUserData.name, true);
        dispatch(
          setRequests(
            users.map((row: any) =>
              row.id !== editUserData.id ? row : editUserData
            )
          )
        );
      })
      .catch(() => Toast("       ", false));
    resetUserData();
    setShow(false);
  };

  return (
    <Modal show={show} onHide={handleClose} backdrop="static" keyboard={false}>
      <Modal.Header
        style={{ display: "flex", alignItems: "center" }}
        closeButton
      >
        <Modal.Title></Modal.Title>
      </Modal.Header>
      <Form onSubmit={handleClickSubmit} className="forget-password-page3-form">
        {editAndAddInputs.map(({ nameInput, type, value, name }, i) => (
          <InputGroup size="sm" className="mb-1 pl-3 pr-3 pt-2" key={i}>
            <InputGroup.Prepend>
              <InputGroup.Text
                // style={{ width: "90px" }}
                id="inputGroup-sizing-sm"
              >
                {nameInput}
              </InputGroup.Text>
            </InputGroup.Prepend>
            <FormControl
              disabled={editUserData.id === 1 && name === "name"}
              value={value}
              name={name}
              onChange={({ target: { value, name } }) =>
                dispatch(
                  setEditContributorData({
                    ...editUserData,
                    [name]: value,
                  })
                )
              }
              type={type}
              aria-label="Small"
              aria-describedby="inputGroup-sizing-sm"
              autoComplete="off"
            />
          </InputGroup>
        ))}
        <fieldset>
          <Form.Group as={Row}>
            <Col className="radio-group">
              <div className="female-male">
                <Form.Check
                  type="radio"
                  name="male"
                  id="maleRadio"
                  checked={editUserData.gender}
                  onChange={({ target: { name } }) =>
                    dispatch(
                      setEditContributorData({
                        ...editUserData,
                        gender: true,
                      })
                    )
                  }
                />
                <Form.Label as="legend" column>
                  
                </Form.Label>
              </div>
              <div className="female-male">
                <Form.Check
                  type="radio"
                  name="female"
                  id="femaleRadio"
                  checked={!editUserData.gender}
                  onChange={({ target: { name } }) =>
                    dispatch(
                      setEditContributorData({
                        ...editUserData,
                        gender: false,
                      })
                    )
                  }
                />
                <Form.Label as="legend" column>
                  
                </Form.Label>
              </div>
            </Col>
          </Form.Group>
        </fieldset>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            
          </Button>
          <Button type="submit" variant="success">
            
          </Button>
        </Modal.Footer>
      </Form>
    </Modal>
  );
};

export default EditUserModal;
