const editUserInputsData = (user: any) => [
  {
    nameInput: "",
    type: "name",
    name: "name",
    value: user["name"],
  },
  {
    nameInput: " ",
    type: "email",

    name: "email",
    value: user["email"],
  },
  {
    nameInput: " ",
    type: "text",
    name: "mobnum",
    value: user["mobnum"],
  },
  {
    nameInput: " ",
    type: "date",
    name: "date of birth",
    value: user["date of birth"]?.split("T")[0],
  },
];
export default editUserInputsData;
