import React from "react";
import { Modal, Button } from "react-bootstrap";
import axios from "axios";
import { Toast } from "../toaster/toaser";
import { useDispatch, useSelector } from "react-redux";
import { setRequests } from "./../../redux/requests/requests-actions";

interface DeleteModalProps {
  show: boolean;
  setShow: (show: boolean) => void;
  resetUserData: () => void;
}

const DeleteUserModal = ({
  show,
  setShow,
  resetUserData,
}: DeleteModalProps) => {
  const deleteUserData = useSelector(
    (state: any) => state.table.editContributorData
  );
  const users = useSelector((state: any) => state.requests.requests);
  const dispatch = useDispatch();
  const handleClickDelete = () => {
    axios
      .post("/deleteUser", deleteUserData)
      .then(() => {
        Toast("  " + deleteUserData.name, true);
        dispatch(
          setRequests(users.filter((usr: any) => usr.id !== deleteUserData.id))
        );
      })
      .catch(() => Toast("       ", false));
    resetUserData();
    setShow(false);
  };

  const handleClose = () => {
    resetUserData();
    setShow(false);
  };

  return (
    <Modal show={show} onHide={handleClose} backdrop="static" keyboard={false}>
      <Modal.Header
        style={{ display: "flex", alignItems: "center" }}
        className="headerCloseButton"
        closeButton
      >
        <Modal.Title></Modal.Title>
      </Modal.Header>
      <Modal.Body>    </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={handleClose}>
          
        </Button>
        <Button onClick={handleClickDelete} variant="danger">
          
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

export default DeleteUserModal;
